export default {
	data() {
		return {
			isactive:1,
			isactive1:1,
			isscene:false,
			iswhite:true,
			isproduct:false,
			isselect:false,
			h2:'',
			h3:'',
			h1:'',
			h:''
		}
	},
	onReady(){
		uni.getSystemInfo({
			 success:  (res) => {   
				this.h2 = res.windowWidth
			}
		}),
		this.getNodesInfo();
	},
	onLoad(){
		
		uni.showLoading({
			title:"加载中..."
		})
		setTimeout(()=>{
			// plus.screen.unlockOrientation();
			plus.screen.lockOrientation('landscape-primary');
			this.iswhite = false;
			uni.hideLoading();
			plus.navigator.setFullscreen(true);
		},1200)
	},
	
	methods: {
		sence(index){
			this.isactive = index
		},
		sence1(index){
			this.isactive1 = index
		},
		selectsence(){
			this.isscene = true
		},
		closesence(){
			this.isscene = false
		},
		closeproduct(){
			this.isproduct = false;
		},
		closeselect(){
			this.isproduct = false;
			this.isselect = false;
		},
		back(){
			plus.screen.unlockOrientation();
			plus.screen.lockOrientation("portrait-primary");
			uni.switchTab({
			    url: '/pages/my/my'
			});
			plus.navigator.setFullscreen(false);
		},
		selectproduct(){
			this.isproduct = true;
			this.isselect = true;
		},
		getNodesInfo(){
			let info2 = uni.createSelectorQuery().select(".p");
			info2.boundingClientRect().exec((res)=>{
				this.h = res[0].height;
			})
			// console.log(this.h)
			let info1 = uni.createSelectorQuery().select(".p1");
			info1.boundingClientRect().exec((res)=>{
				this.h1 = res[0].height;
			})
			let info3 = uni.createSelectorQuery().select(".select");
			info3.boundingClientRect().exec((res)=>{
				this.h3 = res[0].width;
			})
		}
	}
}
